/* Slideshow */
.slider {
  margin: 0 auto;
  overflow: hidden;
  max-width: 700px;
}

.slideSlider {
  white-space: nowrap;
  transition: ease 1000ms;
}

.slide {
  display: inline-block;
  height: 400px;
  width: 100%;
  border-radius: 20px;
}

/* Buttons */
.slideDots {
  text-align: center;
}

.slideDot {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  cursor: pointer;
  margin: 15px 7px 0px;
  background-color: #c4c4c4;
}

.slideDot.active {
  background-color: #000;
}

.slide-content {
  position: relative;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  opacity: 0;
}
.slide-content:hover {
  opacity: 1;
}

.title-Project {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  color: black;
}

.slide-audio-visuel {
  display: inline-block;
  height: 400px;
  width: 100%;
  border-radius: 20px;
  background-repeat: no-repeat;
  background-size: cover;
}

