@mixin Borderradius($radiusSize){
    border-radius: $radiusSize;
} 
    
@mixin setAnimation($size, $color){
    height: $size;
    animation-fill-mode: forwards;
    position: absolute;
    background-color: $color;
    @include Borderradius(24px);
}

@mixin BorderProgressionBar($radiusSize, $shadowSize, $color){
    border-radius: $radiusSize;
    box-shadow: $shadowSize;
    background-color: $color;   
}

@mixin AnimationIntro($start, $end){
    @keyframes AnimationIntro {
        from {
            opacity: 0;
            margin-top: $start;
        }
        to {
            opacity: 1;
            margin-top: $end;
        }
    } 
}

@mixin AnimationToTheLeft($start, $end){
    from {
        margin-left: $start;
    }
    to{
        margin-left: $end;
    }
}