.nav-bar-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 100px;
  color: white;
  background: #282c34;
  width: 100%;
  bottom: 0;
  margin-top: 200px;
}
.nav-bar-footer a {
  text-decoration: none;
  text-decoration-color: none;
}

.footer-children {
  flex-shrink: 0;
}

.logo {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  margin-left: auto;
}

.search-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  width: 300px;
}

.Link-resume {
  text-align: center;
  justify-content: center;
  margin-left: 20px;
}
.Link-resume a {
  text-decoration: none;
  color: white;
}

.link-page {
  padding: 0;
}


