@import "_method.scss";
@import "_variable.scss";

.presentation{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.presentation-title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.text-presentation{
    display:flex;
    flex-direction: row;
    text-align: justify;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    width: 100%;
    margin: auto;
}

.text{
    width: 30%;
    margin-left: 10px;
}

.img-box{
    height: 200px;
    width: 200px;
    display: flex;
    flex-direction: row;
    background-image: url("https://avatars.githubusercontent.com/u/83732366?v=4");
    border-radius: 50%;
    background-size: cover;
    margin: 0;
}

.presentation-content{
    display: flex;
    flex-direction: row;
}